import styles from "../Register.module.css";
import { ingredientPropType } from "../../../utils/prop-types";
import { useRef, useState } from "react";
import {
    Button,
    Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { stellarApi } from "../../../services/rtk/rtkQuerry/stellarApi";

function ForgotPassword() {
    const [valueLogin, setValueLogin] = useState("");
    const [resetPasswordQuery] = stellarApi.useResetPasswordMutation();
    const navigate = useNavigate();

    const onSubmit = () => {
        resetPasswordQuery(valueLogin);
        navigate("/reset-password", { state: "forgotToReset" });
    };

    const { data: auth, isFetching } = stellarApi.useGetUserQuery();

    if (isFetching && !auth) {
        return null;
    }

    if (auth) {
        return <Navigate to="/" replace />;
    }

    return (
        <section className={styles.wrapper}>
            <h2 className={`text text_type_main-medium`}>
                 
            </h2>
            <Input
                type={"text"}
                placeholder={" e-mail"}
                onChange={(e) => setValueLogin(e.target.value)}
                // icon={"CurrencyIcon"}
                value={valueLogin}
                name={"email"}
                error={false}
                // onIconClick={onIconClick}
                errorText={""}
                size={"default"}
                extraClass="ml-1 pt-6"
            />
            <Button
                disabled={!valueLogin}
                extraClass="mt-6"
                htmlType="button"
                type="primary"
                size="large"
                onClick={onSubmit}
            >
                
            </Button>

            <p
                className={`text text_type_main-default text_color_inactive pt-20`}
            >
                 ?
                <Link to="/login" className={styles.link}>
                    
                </Link>
            </p>
        </section>
    );
}

ForgotPassword.propTypes = {
    // ingredient: ingredientPropType.isRequired,
};

export default ForgotPassword;
