import styles from "../Register.module.css";
import { ingredientPropType } from "../../../utils/prop-types";
import { useRef, useState } from "react";
import {
    Button,
    Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Navigate, useLocation, useNavigate } from "react-router-dom";
import { stellarApi } from "../../../services/rtk/rtkQuerry/stellarApi";

function ResetPassword() {
    const [valuePassword, setValuePassword] = useState("");
    const [valueCode, setValueCode] = useState("");
    const inputPasswordRef = useRef(null);
    const [showPassword, setShowPassword] = useState(false);

    const [confirmNewPasswordQuery, { data }] =
        stellarApi.useConfirmNewPassMutation();
    const navigate = useNavigate();
    const location = useLocation();

    const onSubmit = () => {
        confirmNewPasswordQuery({ password: valuePassword, token: valueCode });
        if (data?.success) {
            navigate("/login");
        }
    };

    // console.log(hist);

    const onIconClick = () => {
        setTimeout(() => inputPasswordRef.current.focus(), 0);
        setShowPassword(!showPassword);
    };

    const { data: auth, isFetching } = stellarApi.useGetUserQuery();

    if (isFetching && !auth) {
        return null;
    }

    if (auth || location.state !== "forgotToReset") {
        return <Navigate to="/" replace />;
    }

    return (
        <section className={styles.wrapper}>
            <h2 className={`text text_type_main-medium`}>
                 
            </h2>
            <Input
                type={showPassword ? "text" : "password"}
                placeholder={"  "}
                onChange={(e) => setValuePassword(e.target.value)}
                icon={showPassword ? "HideIcon" : "ShowIcon"}
                value={valuePassword}
                name={"newPassword"}
                error={false}
                ref={inputPasswordRef}
                onIconClick={onIconClick}
                errorText={""}
                size={"default"}
                extraClass="ml-1 pt-6"
            />
            <Input
                type={"text"}
                placeholder={"   "}
                onChange={(e) => setValueCode(e.target.value)}
                // icon={"CurrencyIcon"}
                value={valueCode}
                name={"code"}
                error={false}
                // onIconClick={onIconClick}
                errorText={""}
                size={"default"}
                extraClass="ml-1 pt-6"
            />
            <Button
                disabled={!valueCode || !valuePassword}
                extraClass="mt-6"
                htmlType="button"
                type="primary"
                size="large"
                onClick={onSubmit}
            >
                
            </Button>

            <p
                className={`text text_type_main-default text_color_inactive pt-20`}
            >
                 ?
                <Link to="/login" className={styles.link}>
                    
                </Link>
            </p>
        </section>
    );
}

ResetPassword.propTypes = {
    // ingredient: ingredientPropType.isRequired,
};

export default ResetPassword;
